body
{
    background-color: rgb(30, 32, 32);
}
.container 
{
    max-width: 24rem;
    min-width: 12rem;
    height: 500px;
    margin: 30px auto;
    border-radius: 10px;
    padding: 10px;
    background: linear-gradient(180deg, rgb(89, 0, 255) , rgb(131, 119, 238));
}
.top-bar
{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin: 30px 0 0 ;
}
.top-bar input
{
    width: 230px;
    height: 50px;
    border: none;
    border-radius: 40px;
    font-size: 18px;
    word-break: break-all;
    outline: none;
    padding-left: 30px;
}
.search-icon
{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    cursor: pointer;
    background-color: white;
}
.data-container
{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    gap: 50px;
    color: whitesmoke;
}
.element
{
  display: flex;
  gap: 10px;
}
.weather-temp
{
    font-size: 3rem;
    color: white;
}
.weather-location
{
    font-size: 1.5rem;
     width: 90%;
     word-break: break-all;
     word-wrap: break-word;
     margin: 0 auto;
    color: white;
}
.data
{
    font-size: 1.5rem;
}
.text
{
    font-size: 1rem;
}